import eventList from './event-list.vue';
import draftsList from './drafts-list.vue';
import { uniCalendar } from '@/components/uni-calendar/uni-calendar.vue';
import { dateFormat } from '@/common/dateFormat.js';
export default {
	name: 'mrxc',
	components: {
		eventList,
		draftsList,
		uniCalendar
	},
	data() {
		return {
			CustomBar: this.CustomBar,
			StatusBar: this.StatusBar,
			tabList: [
				{
					title: '事件列表'
				},
				{
					title: '草稿箱'
				}
			],
			tabCur: 0,
			selectTime: ''
		};
	},

	mounted() {
		this.tabCur = this.tabList[0].title;
		this.selectTime = dateFormat(new Date(), 'yyyy-MM-dd');
	},
	methods: {
		tabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
		},
		confirm(e) {
			this.selectTime = e.fulldate;
		},
		openCalendar() {
			this.$refs.calendar.open();
		}
	}
};
